.App {
    text-align: center;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
    -webkit-flex: 0 1 auto;
            flex: 0 1 auto;
}

.App-title {
    font-size: 1.5em;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.centered-container {
    width: 100%;
    text-align: center;
}

.inline-element {
    display: inline-block;
}

.myDiagram {
    width: 70%;
    height: 100%;
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    margin: auto;
}

canvas {
    outline: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0); /* mobile webkit */
}

body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    height: 100%;
    min-height: 100%;
    margin: 0;
}

html {
    height: 100%;
}

.gshHeader {
    border-color: #eabadb;
    font-size: 15;
    margin-bottom: 10;
}

